import React, { useState, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import { ReadOnlyContext } from '../App.js';

const CreateClass = () => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [_class, setClass] = useState({
        'name': '',
        'prof': '',
        'start_date': '',
        'end_date': ''
    });
    const [creationState, setCreationState] = useState('');

    // Handle form input change

    const handleInput = (e, field) => {
        e.persist();
        setClass(prevClass => {
            return {...prevClass, [field]: e.target.value};
        });
    };

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setCreationState('LOADING');
        try{
            let data = await fetch('/api/class', {
                'method': 'POST',
                'mode': 'cors',
                'headers': {
                    'content-type': 'application/json'
                },
                'body': JSON.stringify(_class)
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setClass(prevClass => ({...prevClass, '_id': response._id}));
            setCreationState('SUCCESS');
        }catch(err){
            setCreationState('ERROR');
            console.log(err);
        }
    }

    // Render status message

    let creationStatus;
    if(creationState === 'LOADING') creationStatus = <div>Loading</div>;
    else if(creationState === 'SUCCESS') creationStatus = <div>Success! <Link className="modal-redirect" to={`/class/${_class._id}`} onClick={() => setModalIsOpen(false)}>Go to created class</Link></div>;
    else if(creationState === 'ERROR') creationStatus = <div>Please fill in all the required fields!</div>;

    return (
        <>
            <div className="flex-item" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>
                <div className="flex-inner-title">
                    Create a new class
                </div>
                <div className="add-class">
                    <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 24 24"><path d="M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z"/></svg>
                </div>
            </div>
            <div>
                <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                    <div className="close-modal"><button className="close-modal" onClick={() => setModalIsOpen(false)}>X</button></div>
                    <div className="modal-title">
                        Create class
                    </div>
                    <form className="modal-form">
                        <div>
                            <label htmlFor="name">*Name: </label>
                            <input type="text" value={_class.subject} onChange={e => handleInput(e, 'name')} name="name" id="name"></input>
                        </div>
                        <div>
                            <label htmlFor="prof">Professor: </label>
                            <input type="text" value={_class.duedate} onChange={e => handleInput(e, 'prof')} name="prof" id="prof"></input>
                        </div>
                        <div>
                            <label htmlFor="start_date">*Start date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={_class.description} onChange={e => handleInput(e, 'start_date')} name="start_date" id="start_date"></input>
                        </div>
                        <div>
                            <label htmlFor="end_date">*End date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={_class.sidenote} onChange={e => handleInput(e, 'end_date')} name="end_date" id="end_date"></input>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Submit</button>
                    </form>
                    <div className="modal-status">
                        {creationStatus}
                    </div>
                </Modal>
            </div>
        </>
    );
};

export default CreateClass;
