import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import AddTodo from './AddTodo.js';
import Modal from 'react-modal';
import Cookies from 'universal-cookie';

const Nav = (props) => {

    const cookies = new Cookies();

    const [readOnly, setReadOnly] = [props.readOnly, props.setReadOnly];
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [accessCode, setAccessCode] = useState('');
    const [codeStatus, setCodeStatus] = useState('');

    const removeCookie = () => {
        cookies.remove('code');
        setReadOnly(true);
    };

    const handleSubmit = async e => {
        e.preventDefault();
        setCodeStatus('LOADING');
        let data = await fetch('/api/login', {
            'method': 'POST',
            'mode': 'cors',
            'headers': {
                'content-type': 'application/json'
            },
            'body': JSON.stringify({code: accessCode})
        })
        if(data.status === 403){
            setCodeStatus('ERROR');
        }else{
            setCodeStatus('SUCCESS');
            setReadOnly(false);
            let code = await data.json();
            cookies.set('code', code, {path: '/'});
        }
    };

    let status;
    switch(codeStatus){
        case 'LOADING':
            status = <div>Loading...</div>
            break;
        case 'ERROR':
            status = <div>The code you entered is incorrect!</div>
            break;
        case 'SUCCESS':
            status = <div>You are granted editing permission! <Link className="modal-redirect" to='#' onClick={() => setModalIsOpen(false)}>Close modal</Link></div>
    };

    return (
        <div className="nav">
            <Link to="/">Home</Link>
            <Link to="/class">Classes</Link>
            <Link to="/todo">Todos</Link>
            <AddTodo />
            {readOnly && <Link className="nav-edit-enable" onClick={() => setModalIsOpen(true)} to='#'>Enable editing</Link>}
            {!readOnly && <Link className="nav-edit-disable" onClick={() => removeCookie()} to='#'>Disable editing</Link>}
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content-small" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="modal-title">
                    Enter the access code here:
                </div>
                <input value={accessCode} onChange={e => setAccessCode(e.target.value)}></input>
                <div style={{margin: '10px'}}>
                    <button onClick={e => handleSubmit(e)} className="button-main" type="submit">Submit</button>
                </div>
                <div className="modal-status">
                    {status}
                </div>
            </Modal>
        </div>
    );
};

export default Nav;
