import React from 'react';
import { Link } from 'react-router-dom';

const TodoElement = ({ _id, subject, duedate, description, sidenote, is_done }) => {
    return (
        <div className="flex-item" onClick={() => window.location.replace(`/todo/${_id}`)}>
            <div className="flex-inner-title">
                {subject.length > 20 ? `${subject.substring(0,20)}...` : subject}
            </div>
            <div className="flex-inner-text">
                <div><strong>Due date: </strong>{duedate}</div>
                <div><strong>Description: </strong>{description.length > 20 ? `${description.substring(0, 20)}...` : description} </div>
                <div><strong>Side notes: </strong>{sidenote ? (sidenote.length > 20 ? `${sidenote.substring(0,20)}...` : sidenote) : 'None'} </div>
                <div>
                    {(duedate < new Date().toISOString().split("T")[0] && !is_done) && 'The due date of this todo has passed!'}
                </div>
                <div>Click to view details!</div>
            </div>
        </div>
    );
};

export default TodoElement;
