import React, { useState, useEffect } from 'react';
import NoteElement from '../components/NoteElement.js';
import EditClass from '../components/EditClass.js';
import RemoveClass from '../components/RemoveClass.js';
import AddNote from '../components/AddNote.js';

const ClassDetail = ({ match }) => {

    // Set state variable

    const [_class, setClass] = useState({});
    const [classFound, setClassFound] = useState(false);
    const [loading, setLoading] = useState(true);
    const [sortBy, setSortBy] = useState('DATE_ASCENDING');

    // Fetch class from DB

    useEffect(() => {
        const fetchClass = async () => {
            try{
                let data = await fetch(`/api/class/${match.params.classID}`);
                let _class = await data.json();
                setLoading(false);
                if(data.ok) setClassFound(true);
                if(!data.ok) throw new Error(_class);
                switch(sortBy){
                    case 'DATE_ASCENDING':
                        _class.notes.sort((noteA, noteB) => {
                            if(noteA.date > noteB.date) return 1;
                            if(noteA.date < noteB.date) return -1;
                            return 0;
                        });
                        break;
                    case 'DATE_DESCENDING':
                        _class.notes.sort((noteA, noteB) => {
                            if(noteA.date < noteB.date) return 1;
                            if(noteA.date > noteB.date) return -1;
                            return 0;
                        });
                        break;
                    case 'NOTE_LENGTH':
                        _class.notes.sort((noteA, noteB) => {
                            if(noteA.content.length < noteB.content.length) return 1;
                            if(noteA.content.length > noteB.content.length) return -1;
                            return 0;
                        });
                        break;
                };
                setClass(_class);
            }catch(err){
                console.log(err);
            }
        };
        fetchClass();
    }, [sortBy]);

    // Handle sort method change

    const handleChange = e => {
        e.persist();
        setSortBy(e.target.value);
    }

    if(loading) return <div className="page-title">Loading...</div>
    if(!classFound) return <div className="not-found">Class not found!</div>

    return (
        <div>
            <div className="page-title">
                Class Details
            </div>
            <div className="page-info">
                <div className="value">
                    {_class.name}
                </div>
                <label className="key">Name</label>
                <div className="value">
                    {_class.prof || 'N/A'}
                </div>
                <label className="key">Professor</label>
                <div className="value">
                    {_class.start_date}
                </div>
                <label className="key">Start date</label>
                <div className="value">
                    {_class.end_date}
                </div>
                <label className="key">End date</label>
            </div>
            <div className="page-subtitle">
                Notes for this class
            </div>
            <div className="custom-select">
                <select defaultValue="DATE_ASCENDING" onChange={e => handleChange(e)}>
                    <option value="DATE_ASCENDING">Date ascending</option>
                    <option value="DATE_DESCENDING">Date descending</option>
                    <option value="NOTE_LENGTH">Note length</option>
                </select>
                <span className="custom-arrow"></span>
            </div>
            <div className="flex-container">
                <AddNote _id={_class._id} />
                {!(_class.notes === undefined || _class.notes.length === 0) && _class.notes.map(note => (<NoteElement key={note._id} classID={match.params.classID} _id={note._id} title={note.title} content={note.content} resource={note.resource} sidenote={note.sidenote} date={note.date} />))}
            </div>
            <div>
                <EditClass _id={_class._id} name={_class.name} prof={_class.prof} start_date={_class.start_date} end_date={_class.end_date} />
                <RemoveClass _id={_class._id} name={_class.name} prof={_class.prof} start_date={_class.start_date} end_date={_class.end_date} />
            </div>
        </div>
    );
};

export default ClassDetail;
