import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';

const RemoveTodo = ({ _id, subject, duedate, description, sidenote }) => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [deletionState, setDeletionState] = useState('');

    // Handle form submission

    const handleConfirm = async e => {
        e.preventDefault();
        setDeletionState('LOADING');
        try{
            let data = await fetch(`/api/todo/${_id}`, {
                'method': 'DELETE',
                'mode': 'cors'
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setDeletionState('SUCCESS');
        }catch(err){
            setDeletionState('ERROR');
            console.log(err);
        }
    };

    // Handle status message

    let deletionStatus;
    if(deletionState === 'LOADING') deletionStatus = <div>Loading</div>;
    else if(deletionState === 'SUCCESS') deletionStatus = <div>Successfully removed todo! <Link to="/todo" className="modal-redirect" onClick={() => setModalIsOpen(false)}>Go back to todos list</Link></div>;
    else if(deletionState === 'ERROR') deletionStatus = <div>Error! Check console for details</div>;


    return (
        <div>
            <div>
                <button className="button-main" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>Remove Todo</button>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to remove the following todo?
                </div>
                <div className="modal-list">
                    <ul>
                        <li>Subject: {subject}</li>
                        <li>Due date: {duedate}</li>
                        <li>Description: {description}</li>
                        <li>Side notes: {sidenote || 'None'}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" onClick={e => handleConfirm(e)}>Confirm</button>
                    <button className="button-main" type="submit" onClick={() => {setModalIsOpen(false);}}>Cancel</button>
                </div>
                <div className="modal-status">
                    {deletionStatus}
                </div>
            </Modal>
        </div>
    );
};

export default RemoveTodo;
