import React, { useState, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import { ReadOnlyContext } from '../App.js';

Modal.setAppElement('#root')

const AddTodoMain = () => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [todo, setTodo] = useState({
        'subject': '',
        'duedate': '',
        'description': '',
        'sidenote': ''
    });
    const [creationState, setCreationState] = useState('');

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setCreationState('LOADING');
        try{
            let data = await fetch('/api/todo', {
                'method': 'POST',
                'mode': 'cors',
                'headers': {
                    'content-type': 'application/json'
                },
                'body': JSON.stringify(todo)
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setCreationState('SUCCESS');
        }catch(err){
            setCreationState('ERROR');
            console.log(err);
        }
    }

    // Handle input changes

    const handleInput = (e, field) => {
        e.persist();
        setTodo(prevTodo => {
            return {...prevTodo, [field]: e.target.value};
        });
    };

    // Render status message

    let creationStatus;

    if(creationState === 'LOADING') creationStatus = <div>Loading</div>;
    else if(creationState === 'SUCCESS') creationStatus = (<div>Todo successfully added! <Link className="modal-redirect" to='/redirect?url=/todo' onClick={() => setModalIsOpen(false)}>View todo list</Link></div>);
    else if(creationState === 'ERROR') creationStatus = (<div>Please fill in all the required fields!</div>);

    return (
        <>
            <div className="flex-item" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>
                <div className="flex-inner-title">
                    Add a todo
                </div>
                <div className="add-todo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" viewBox="0 0 24 24"><path d="M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z"/></svg>
                </div>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div>
                    <div className="close-modal"><button onClick={() => setModalIsOpen(false)}>X</button></div>
                    <div className="modal-title">
                        Add todo
                    </div>
                    <form className="modal-form">
                        <div>
                            <label htmlFor="subject">*Subject: </label>
                            <input type="text" value={todo.subject} onChange={e => handleInput(e, 'subject')} name="subject" id="subject"></input>
                        </div>
                        <div>
                            <label htmlFor="duedate">*Due date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={todo.duedate} onChange={e => handleInput(e, 'duedate')} name="duedate" id="duedate"></input>
                        </div>
                        <div>
                            <label htmlFor="description">*Description: </label>
                            <textarea value={todo.description} onChange={e => handleInput(e, 'description')} name="description" id="description"></textarea>
                        </div>
                        <div>
                            <label htmlFor="sidenote">Sidenotes: </label>
                            <textarea value={todo.sidenote} onChange={e => handleInput(e, 'sidenote')} name="sidenote" id="sidenote"></textarea>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Submit</button>
                    </form>
                    <div className="modal-status">
                        {creationStatus}
                    </div>
                </div>
            </Modal>
        </>
    );
};


export default AddTodoMain;
