import React, { useState, useContext } from 'react';
import Modal from 'react-modal';
import { Link } from 'react-router-dom';
import { ReadOnlyContext } from '../App.js';

const RemoveNote = ({ classID, _id, title, content, resource, sidenote, date }) => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [deletionState, setDeletionState] = useState('');

    // Handle form submission

    const handleSubmit = async e => {
        e.preventDefault();
        setDeletionState('LOADING');
        try{
            let data = await fetch(`/api/note/${classID}/${_id}`, {
                'method': 'DELETE',
                'mode': 'cors'
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setDeletionState('SUCCESS');
        }catch(err){
            setDeletionState('ERROR');
            console.log(err);
        }
    }

    // Handle status message

    let deletionStatus;
    if(deletionState === 'LOADING') deletionStatus = <div>Loading</div>
    else if(deletionState === 'ERROR') deletionStatus = <div>Error! Check console for details</div>
    else if(deletionState === 'SUCCESS') deletionStatus = <div>Note successfully removed! <Link className="modal-redirect" to={`/class/${classID}`} onClick={() => setModalIsOpen(false)}>Go back to class</Link></div>

    return (
        <div>
            <button className="button-main" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>Remove this note</button>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to remove the following note?
                </div>
                <div className="modal-list">
                    <ul>
                        <li>Title: {title}</li>
                        <li>Content: {content}</li>
                        <li>Resources: {resource}</li>
                        <li>Side notes: {sidenote}</li>
                        <li>Date created: {date && date.split('T')[0]}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" type="submit" onClick={e => handleSubmit(e)}>Confirm</button>
                    <button className="button-main" onClick={() => setModalIsOpen(false)}>Cancel</button>
                </div>
                <div className="modal-status">
                    {deletionStatus}
                </div>
            </Modal>
        </div>
    );
};

export default RemoveNote;
