import React from 'react';
import { Link } from 'react-router-dom';

const NoteElement = ({ _id, classID, title, content, resource, sidenote, date }) => {
    return (
        <div className="flex-item" onClick={() => window.location.replace(`/class/${classID}/${_id}`)}>
            <div className="flex-inner-title">
                {title.length > 20 ? `${title.substring(0,20)}...` : title}
            </div>
            <div className="flex-inner-text">
                <div><strong>Content: </strong>{content.length > 20 ? `${content.substring(0,20)}...` : content}</div>
                <div><strong>Extra resources: </strong>{resource ? (resource.length > 17 ? `${resource.substring(0,17)}...` : resource) : 'None'}</div>
                <div><strong>Side notes: </strong>{sidenote ? (sidenote.length > 20 ? `${sidenote.substring(0,20)}...` : sidenote) : 'None'}</div>
                <div><strong>Date added: {date.split('T')[0]}</strong></div>
                <div>Click to view details!</div>
            </div>
        </div>
    );
};

export default NoteElement;
