import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './pages/Home.js';
import Todo from './pages/Todo.js';
import TodoDetail from './pages/TodoDetail.js';
import Class from './pages/Class.js';
import ClassDetail from './pages/ClassDetail.js';
import NoteDetail from './pages/NoteDetail.js';
import Nav from './components/Nav.js';
import Redirect from './pages/Redirect.js';
import './App.css';
import logo from './logo.png';
import Cookies from 'universal-cookie';

export const ReadOnlyContext = React.createContext('');

function App() {

  const cookies = new Cookies();
  const [readOnly, setReadOnly] = useState(true);

  useEffect(() => {
        const checkPerm = async code => {
            let data = await fetch('/api/login', {
                'method': 'GET',
                'headers': {
                    'TOKEN': code
                }
            });
            if(data.ok){
                return true;
            }else{
                return false;
            };
        };
        if(cookies.get('code')){
            if(checkPerm(cookies.get('code')) === true){
                setReadOnly(false);
            }else{
                setReadOnly(true);
            }
        };
    });

  return (
    <Router>
      <div className="App">
        <Nav readOnly={readOnly} setReadOnly={setReadOnly} />
          <div className="main">
          <div className="logo">
            <img src={logo}></img>
          </div>
            <ReadOnlyContext.Provider value={readOnly}>
              <Switch>
                <Route path="/" exact component={Home} />
                <Route path="/todo" exact component={Todo} />
                <Route path="/todo/:todoID" exact component={TodoDetail} />
                <Route path="/class" exact component={Class} />
                <Route path="/class/:classID" exact component={ClassDetail} />
                <Route path="/class/:classID/:noteID" exact component={NoteDetail} />
                <Route path="/redirect" exact component={Redirect} />
              </Switch>
            </ReadOnlyContext.Provider>
          </div>
      </div>
    </Router>
  );
}

export default App;
