import React, { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';

const EditTodo = (props) => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [editState, setEditState] = useState('');
    const [todo, setTodo] = useState(props);

    useEffect(() => {
        setTodo(props);
    }, [props])

    // Handle form input change

    const handleInput = (e, field) => {
        e.persist();
        setTodo(prevTodo => {
            return {...prevTodo, [field]: e.target.value};
        });
    };

    // Handle form submission

    const handleEdit = async e => {
        e.preventDefault();
        setEditState('LOADING');
        try{
            let data = await fetch(`/api/todo/${props._id}`, {
                'method': 'PATCH',
                'mode': 'cors',
                'headers': {
                    'content-type': 'application/json'
                },
                'body': JSON.stringify(todo)
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setEditState('SUCCESS');
        }catch(err){
            setEditState('ERROR');
            console.log(err);
        }
    }

    // Handle status message

    let editStatus;
    if(editState === 'LOADING') editStatus = <div>Loading</div>;
    else if(editState === 'SUCCESS') editStatus = <div>Success! <Link className="modal-redirect" to={`/redirect?url=/todo/${props._id}`} onClick={() => setModalIsOpen(false)}>View edited todo</Link></div>;
    else if(editState === 'ERROR') editStatus = <div>Please fill in all the required fields!</div>;

    return (
        <div>
            <div>
                <button className="button-main" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>Edit Todo</button>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Edit todo
                </div>
                <form className="modal-form">
                    <div>
                            <label htmlFor="subject">*Subject: </label>
                            <input type="text" value={todo.subject} onChange={e => handleInput(e, 'subject')} name="subject" id="subject"></input>
                        </div>
                        <div>
                            <label htmlFor="duedate">*Due date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={todo.duedate} onChange={e => handleInput(e, 'duedate')} name="duedate" id="duedate"></input>
                        </div>
                        <div>
                            <label htmlFor="description">*Description: </label>
                            <textarea value={todo.description} onChange={e => handleInput(e, 'description')} name="description" id="description"></textarea>
                        </div>
                        <div>
                            <label htmlFor="sidenote">Sidenotes: </label>
                            <textarea value={todo.sidenote} onChange={e => handleInput(e, 'sidenote')} name="sidenote" id="sidenote"></textarea>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleEdit(e)}>Confirm edit</button>
                        <div className="modal-status">
                            {editStatus}
                        </div>
                </form>
            </Modal>
        </div>
    );
};

export default EditTodo;
