import React, { useState, useEffect } from 'react';
import EditTodo from '../components/EditTodo.js';
import RemoveTodo from '../components/RemoveTodo.js';
import UpdateTodoStatus from '../components/MarkTodoAsDone.js';

const TodoDetail = ({ match }) => {

    const [todo, setTodo] = useState({});
    const [loading, setLoading] = useState(true);
    const [todoFound, setTodoFound] = useState(false);

    useEffect(() => {
        const fetchData = async () => {
            try{
                const data = await fetch(`/api/todo/${match.params.todoID}`);
                const todo = await data.json();
                setLoading(false);
                if(data.ok) setTodoFound(true);
                if(!data.ok) throw new Error(todo);
                setTodo(todo);
            }catch(err){
                console.log(err);
            }
        }
        fetchData();
    }, []);

    if(loading) return <div className="page-title">Loading...</div>
    if(!todoFound) return <div className="not-found">Todo not found!</div>

    return (
        <div>
            <div className="page-title">Todo Details</div>
            <div className="page-info">
                <div className="value">
                    {todo.subject}
                </div>
                <label className="key">Subject</label>
                <div className="value">
                    {todo.duedate}
                </div>
                <label className="key">Due date</label>
                <div className="value">
                    {todo.description}
                </div>
                <label className="key">Description</label>
                <div className="value">
                    {todo.sidenote || 'None'}
                </div>
                <label className="key">Side notes</label>
                <div className="value">
                    {todo.is_done ? 'Completed' : 'Ongoing'}
                </div>
                <label className="key">Status</label>
            </div>
            <div>
                <EditTodo _id={todo._id} subject={todo.subject} duedate={todo.duedate} description={todo.description} sidenote={todo.sidenote} />
                <UpdateTodoStatus _id={todo._id} is_done={todo.is_done} subject={todo.subject} duedate={todo.duedate} description={todo.description} sidenote={todo.sidenote} />
                <RemoveTodo _id={todo._id} subject={todo.subject} duedate={todo.duedate} description={todo.description} sidenote={todo.sidenote} />
            </div>
        </div>
    );
};

export default TodoDetail;
