import React, { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';

const EditClass = (props) => {

    // Set state variables

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [editState, setEditState] = useState('');
    const [_class, setClass] = useState(props);

    useEffect(() => {
        setClass(props)
    }, [props]);

    // Handle form input change

    const handleInput = (e, field) => {
        e.persist();
        setClass(prevClass => {
            return {...prevClass, [field]: e.target.value};
        });
    };

    // Handle form submission

    const handleEdit = async e => {
        e.preventDefault();
        setEditState('LOADING');
        try{
            let data = await fetch(`/api/class/${props._id}`, {
                'method': 'PATCH',
                'mode': 'cors',
                'headers': {
                    'content-type': 'application/json'
                },
                'body': JSON.stringify(_class)
            });
            let response = await data.json();
            console.log(response);
            if(!data.ok) throw new Error(response);
            setEditState('SUCCESS');
        }catch(err){
            setEditState('ERROR');
            console.log(err);
        }
    }

    let editStatus;
    if(editState === 'LOADING') editStatus = <div>Loading</div>;
    else if(editState === 'SUCCESS') editStatus = <div>Success! <Link className="modal-redirect" to={`/redirect?url=/class/${props._id}`} onClick={() => setModalIsOpen(false)}>View edited class</Link></div>;
    else if(editState === 'ERROR') editStatus = <div>Please fill in all the required fields!</div>;

    return (
        <div>
            <button className="button-main" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>Edit this class</button>
            <div>
                <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                    <div className="close-modal">
                        <button onClick={() => setModalIsOpen(false)}>X</button>
                    </div>
                    <div className="modal-title">
                        Edit class
                    </div>
                    <form className="modal-form">
                        <div>
                            <label htmlFor="name">*Name: </label>
                            <input type="text" value={_class.name} onChange={e => handleInput(e, 'name')} name="name" id="name"></input>
                        </div>
                        <div>
                            <label htmlFor="prof">Professor: </label>
                            <input type="text" value={_class.prof} onChange={e => handleInput(e, 'prof')} name="prof" id="prof"></input>
                        </div>
                        <div>
                            <label htmlFor="start_date">*Start date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={_class.start_date} onChange={e => handleInput(e, 'start_date')} name="start_date" id="start_date"></input>
                        </div>
                        <div>
                            <label htmlFor="end_date">*End date: </label>
                            <input type="date" min={new Date().toISOString().split("T")[0]} value={_class.end_date} onChange={e => handleInput(e, 'end_date')} name="end_date" id="end_date"></input>
                        </div>
                        <button className="button-main" type="submit" onClick={e => handleEdit(e)}>Confirm edit</button>
                    </form>
                    <div className="modal-status">
                        {editStatus}
                    </div>
                </Modal>
            </div>
        </div>
    );
};

export default EditClass;
