import React, { useState, useEffect } from 'react';
import ClassElement from '../components/ClassElement.js';
import CreateClass from '../components/CreateClass.js';

const Class = () => {

    const [classes, setClasses] = useState([]);
    const [loading, setLoading] = useState(true);
    const [sortBy, setSortBy] = useState('START_DATE_ASCENDING');

    // Fetch classes

    useEffect(() => {
        const fetchClasses = async () => {
            try{
                let data = await fetch('/api/class');
                let classes = await data.json();
                setLoading(false);
                if(!data.ok) throw new Error(classes);
                switch(sortBy){
                    case 'START_DATE_ASCENDING':
                        classes.sort((classA, classB) => {
                            if(classA.start_date > classB.start_date) return  1;
                            if(classA.start_date < classB.start_date) return -1;
                            return 0;
                        });
                        break;
                    case 'START_DATE_DESCENDING':
                        classes.sort((classA, classB) => {
                            if(classA.start_date < classB.start_date) return  1;
                            if(classA.start_date > classB.start_date) return -1;
                            return 0;
                        });
                        break;
                    case 'END_DATE_ASCENDING':
                        classes.sort((classA, classB) => {
                            if(classA.end_date > classB.end_date) return  1;
                            if(classA.end_date < classB.end_date) return -1;
                            return 0;
                        });
                        break;
                    case 'END_DATE_DESCENDING':
                        classes.sort((classA, classB) => {
                            if(classA.end_date < classB.end_date) return  1;
                            if(classA.end_date > classB.end_date) return -1;
                            return 0;
                        });
                        break;
                    case 'NAME':
                        classes.sort((classA, classB) => {
                            if(classA.name > classB.name) return  1;
                            if(classA.name < classB.name) return -1;
                            return 0;
                        });
                        break;
                    case 'NOTES':
                        classes.sort((classA, classB) => {
                            if(classA.notes.length < classB.notes.length) return 1;
                            if(classA.notes.length > classB.notes.length) return -1;
                            return 0;
                        });
                        break;
                }
                setClasses(classes);
            }catch(err){
                console.log(err);
            };
        }
        fetchClasses();
    }, [sortBy])

    // Handle change on sort method

    const handleChange = e => {
        e.persist();
        setSortBy(e.target.value);
    }

    return (
        <div>
            <div className="page-title">
                Classes
            </div>
            <div className="custom-select">
                <select defaultValue="START_DATE_ASCENDING" onChange={e => handleChange(e)}>
                    <option value="START_DATE_ASCENDING">Start date ascending</option>
                    <option value="START_DATE_DESCENDING">Start date descending</option>
                    <option value="END_DATE_ASCENDING">End date ascending</option>
                    <option value="END_DATE_DESCENDING">End date descending</option>
                    <option value="NAME">Name</option>
                    <option value="NOTES">Number of notes</option>
                </select>
                <span className="custom-arrow"></span>
            </div>
            <div className="flex-container">
                <CreateClass />
                {loading && (<div className="flex-item">
                    <div className="flex-inner-title">
                        Loading...
                    </div>
                </div>)}
                {classes.map(_class => (<ClassElement key={_class._id} _id={_class._id} name={_class.name} prof={_class.prof} start_date={_class.start_date} end_date={_class.end_date} notes={_class.notes} />))}
            </div>
        </div>
    );
};

export default Class;
