import React from 'react';
import { Link } from 'react-router-dom';

const ClassElement = ({ _id, name, prof, start_date, end_date, notes }) => {
    return (
        <div className="flex-item" onClick={() => window.location.replace(`/class/${_id}`)}>
            <div className="flex-inner-title">{name.length > 16 ? `${name.substring(0,16)}...` : name}</div>
            <div className="flex-inner-text">
                <div><strong>Professor: </strong>{prof ? (prof.length > 20 ? `${prof.substring(0,20)}...` : prof) : 'N/A'}</div>
                <div><strong>Start date: </strong>{start_date}</div>
                <div><strong>End date: </strong>{end_date}</div>
                <div><strong>Number of notes: </strong>{notes.length}</div>
                <div>
                    Click to view details!
                </div>
            </div>
        </div>
    );
};

export default ClassElement;
