import React, { useState, useContext } from 'react';
import { Link } from 'react-router-dom';
import Modal from 'react-modal';
import { ReadOnlyContext } from '../App.js';

const UpdateTodoStatus = ({ _id, subject, duedate, description, sidenote, is_done }) => {

    const readOnly = useContext(ReadOnlyContext);
    const [modalIsOpen, setModalIsOpen] = useState(false);
    const [updateState, setUpdateState] = useState('');

    const handleConfirm = async e => {
        e.preventDefault();
        setUpdateState('LOADING');
        try{
            let data = await fetch(`/api/todo/${_id}`, {
                'method': 'PATCH',
                'mode': 'cors',
                'headers': {
                    'content-type': 'application/json'
                },
                'body': JSON.stringify({'is_done': !is_done})
            });
            let response = await data.json();
            if(!data.ok) throw new Error(response);
            setUpdateState('SUCCESS');
        }catch(err){
            setUpdateState('ERROR');
            console.log(err);
        }
    };

    let updateStatus;
    if(updateState === 'LOADING') updateStatus = <div>Loading</div>;
    else if(updateState === 'SUCCESS') updateStatus = <div>Success! <Link to='/todo' className="modal-redirect" onClick={() => setModalIsOpen(false)}>Go back to todo list</Link></div>;
    else if(updateState === 'ERROR') updateStatus = <div>Error! Check console for details</div>;

    return (
        <div>
            <div>
                <button className="button-main" onClick={() => {if(!readOnly) setModalIsOpen(true)}}>Mark todo as {is_done ? 'ongoing' : 'complete'}</button>
            </div>
            <Modal isOpen={modalIsOpen} overlayClassName="modal-overlay" className="modal-content" portalClassName="modal" parentSelector={() => document.querySelector('.main')}>
                <div className="close-modal">
                    <button onClick={() => setModalIsOpen(false)}>X</button>
                </div>
                <div className="modal-title">
                    Are you sure you want to mark the following todo as {is_done ? 'ongoing' : 'complete'}?
                </div>
                <div className="modal-list">
                    <ul>
                        <li>Subject: {subject}</li>
                        <li>Due date: {duedate}</li>
                        <li>Description: {description}</li>
                        <li>Side notes: {sidenote || 'None'}</li>
                    </ul>
                </div>
                <div>
                    <button className="button-main" onClick={e => handleConfirm(e)}>Confirm</button>
                    <button className="button-main" type="submit" onClick={() => {setModalIsOpen(false);}}>Cancel</button>
                </div>
                <div className="modal-status">
                    {updateStatus}
                </div>
            </Modal>
        </div>
    );
};

export default UpdateTodoStatus;
