import React, { useState, useEffect } from 'react';
import TodoElement from '../components/TodoElement.js';
import AddTodoMain from '../components/AddTodoMain.js'

const Todo = () => {

    // Set state variables

    const [todos, setTodos] = useState([]);
    const [completedTodos, setCompletedTodos] = useState([]);
    const [loading, setLoading] = useState(true);
    const [sortBy, setSortBy] = useState('DATE_ASCENDING');

    // Fetch todo on sort changes

    useEffect(() => {
        const fetchData = async () => {
            try{
                const data = await fetch('/api/todo');
                const todos = await data.json();
                setLoading(false);
                if(!data.ok) throw new Error(todos);
                switch(sortBy){
                    case 'DATE_ASCENDING':
                        todos.sort((todoA, todoB) => {
                            if(todoA.duedate > todoB.duedate) return 1;
                            if(todoA.duedate < todoB.duedate) return -1;
                            return 0;
                        });
                        break;
                    case 'DATE_DESCENDING':
                        todos.sort((todoA, todoB) => {
                            if(todoA.duedate < todoB.duedate) return 1;
                            if(todoA.duedate > todoB.duedate) return -1;
                            return 0;
                        });
                        break;
                    case 'SUBJECT':
                        todos.sort((todoA, todoB) => {
                            if(todoA.subject > todoB.subject) return 1;
                            if(todoA.subject < todoB.subject) return -1;
                            return 0;
                        });
                        break;
                };
                setTodos(todos.filter(todo => !todo.is_done));
                setCompletedTodos(todos.filter(todo => todo.is_done));
            }catch(err){
                console.log(err);
            };
        };
        fetchData();
    }, [sortBy]);

    // Handle change in sort method

    const handleChange = e => {
        e.persist();
        setSortBy(e.target.value);
    }

    return (
        <div>
            <div className="page-title">Todos</div>
            <div className="custom-select">
                <select onChange={e => handleChange(e)} defaultValue="DATE_ASCENDING">
                    <option value="DATE_ASCENDING">Date ascending</option>
                    <option value="DATE_DESCENDING">Date descending</option>
                    <option value="SUBJECT">Subject</option>
                </select>
                <span className="custom-arrow"></span>
            </div>
            <div>
                <div className="page-subtitle">Current todos</div>
                    <div className="flex-container">
                        <AddTodoMain />
                        {loading && (
                            <div className="flex-item">
                                <div className="flex-inner-title">
                                  Loading...
                                </div>
                            </div>)}
                        {todos.map(todo => (<TodoElement key={todo._id} _id={todo._id} subject={todo.subject} duedate={todo.duedate} description={todo.description} sidenote={todo.sidenote} is_done={false}/>))}
                    </div>
            </div>
            <div>
                <div className="page-subtitle">Completed todos</div>
                {!(completedTodos.length === 0) ? (
                    <div className="flex-container">
                        {completedTodos.map(todo => (
                            <TodoElement key={todo._id} _id={todo._id} subject={todo.subject} duedate={todo.duedate} description={todo.description} sidenote={todo.sidenote} is_done={true}/>
                        ))}
                    </div>
                ) : (
                    <div className="not-found">
                        There are currently no completed todos!
                    </div>
            )}
            </div>
        </div>
    );
};

export default Todo;
