import React, { useState, useEffect } from 'react';
import EditNote from '../components/EditNote.js';
import RemoveNote from '../components/RemoveNote.js';

const NoteDetail = ({ match }) => {

    const [note, setNote] = useState({});
    const [loading, setLoading] = useState(true);
    const [noteFound, setNoteFound] = useState(false);

    useEffect(() => {
        const fetchNote = async () => {
            try{
                let data = await fetch(`/api/note/${match.params.classID}/${match.params.noteID}`);
                let note = await data.json();
                setLoading(false);
                if(data.ok) setNoteFound(true)
                if(!data.ok) throw new Error(note);
                setNote(note);
            }catch(err){
                console.log(err);
            };
        };
        fetchNote();
    }, [match.params.classID, match.params.noteID]);

    if(loading) return <div className="page-title">Loading...</div>
    if(!noteFound) return <div className="not-found">Note not found!</div>

    return (
        <div>
            <div>
                <div className="page-title">
                    Note Details
                </div>
                <div className="page-info">
                    <div className="value">
                        {note.title}
                    </div>
                    <label className="key">Title</label>
                    <div className="value">
                        {note.content}
                    </div>
                    <label className="key">Content</label>
                    <div className="value">
                        {note.resource || 'None'}
                    </div>
                    <label className="key">Resources</label>
                    <div className="value">
                        {note.sidenote || 'None'}
                    </div>
                    <label className="key">Side notes</label>
                    <div className="value">
                        {note.date && note.date.split('T')[0]}
                    </div>
                    <label className="key">Date added</label>
                </div>
            </div>
            <div>
                <button className="button-main" onClick={() => document.location.replace(`/class/${match.params.classID}`)}>Go back to class</button>
                <EditNote classID = {match.params.classID} _id={note._id} title={note.title} content={note.content} resource={note.resource} sidenote={note.sidenote} date={note.date} />
                <RemoveNote classID = {match.params.classID} _id={note._id} title={note.title} content={note.content} resource={note.resource} sidenote={note.sidenote} date={note.date} />
            </div>
        </div>
    );
};

export default NoteDetail;
